import React, { useState, useEffect } from 'react';
import PlayerTable from './PlayerTable';
import TeamDisplay from './TeamDisplay';
import Footer from './Footer';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import { v4 as uuidv4 } from 'uuid';

function App() {
  const [numPlayers, setNumPlayers] = useState(10);
  const [numTeams, setNumTeams] = useState(3);
  const [players, setPlayers] = useState(Array.from({ length: 10 }, () => ({
    id: uuidv4(),
    name: '',
    rating: 3,
  })));  
  const [previousPlayers, setPreviousPlayers] = useState([]);
  const [teams, setTeams] = useState([]);
  const [currentVariance, setCurrentVariance] = useState(Infinity);
  const [message, setMessage] = useState('');
  const [showDetails, setShowDetails] = useState(false);
  const [canRandomize, setCanRandomize] = useState(true);
  const [isDescriptionCollapsed, setIsDescriptionCollapsed] = useState(true);
  const [isTableCollapsed, setIsTableCollapsed] = useState(false);
  const [namesText, setNamesText] = useState("");
  const [isTablePopulated, setIsTablePopulated] = useState(false);
  const [isTeamsAssigned, setIsTeamsAssigned] = useState(false);
  const [previousTeams, setPreviousTeams] = useState([]);
  const [lockCaptains, setLockCaptains] = useState(false);
  const incrementTeams = () => setNumTeams((prev) => Math.min(prev + 1, 4));
  const decrementTeams = () => setNumTeams((prev) => Math.max(prev - 1, 2));


  useEffect(() => {
    if (lockCaptains && teams.length > 0) {
      setPreviousTeams(teams);
    }
  }, [lockCaptains]);  

  const handleNumPlayersChange = (newNumPlayers) => {
    if (newNumPlayers > players.length) {
      const additionalPlayers = Array.from({ length: newNumPlayers - players.length }, () => ({
        id: uuidv4(),
        name: '',
        rating: 3,
      }));
      setPlayers((prevPlayers) => [...prevPlayers, ...additionalPlayers]);
    } else {
      setPlayers((prevPlayers) => prevPlayers.slice(0, newNumPlayers));
    }
    setNumPlayers(newNumPlayers);
  };

  const incrementPlayers = () => handleNumPlayersChange(numPlayers + 1);
  const decrementPlayers = () => handleNumPlayersChange(numPlayers > 1 ? numPlayers - 1 : 1);

  const toggleTableCollapse = () => setIsTableCollapsed(!isTableCollapsed);

  const handleNamesTextChange = (e) => {
    setNamesText(e.target.value);
  };

  const populateTableFromNames = () => {
    const nameLines = namesText.split('\n').filter(line => line.trim() !== '');
    
    const parsedNames = nameLines
      .filter(line => /^\d+\.\s*/.test(line))
      .map(line => {
        const name = line.replace(/^\d+\.\s*/, '').trim();
        return { id: uuidv4(), name, rating: 3 };
      })
      .filter(player => player.name !== '');

    setPlayers(parsedNames);
    setNumPlayers(parsedNames.length);
    setNamesText("");
    setIsTablePopulated(true);
  };

 // The idea here is to calculate and balance teams by distributing strong, weak, and neutral players evenly.
  const calculateTeams = () => {
    if (players.length === 0) {
      setMessage('Please generate player data first.');
      return;
    }
  
    // Save the current teams to previousTeams before creating new ones
    if (teams.length > 0) {
      setPreviousTeams([...teams]);
    }
  
    let captains = [];
    let remainingPlayers = [...players].filter(player => player.name.trim() !== '');
    let newTeams = [];
  
    if (lockCaptains && previousTeams.length > 0) {
      newTeams = previousTeams.map(team => [team[0]]);
      captains = previousTeams.map(team => team[0]);
  
      const captainIds = captains.map(captain => captain.id);
      remainingPlayers = remainingPlayers.filter(player => !captainIds.includes(player.id));
    } else {
      remainingPlayers = [...players];
      newTeams = Array.from({ length: numTeams }, () => []);
  
      const shuffledPlayers = shuffleArray(remainingPlayers);
      captains = shuffledPlayers.slice(0, numTeams);
      remainingPlayers = shuffledPlayers.slice(numTeams);

      newTeams = captains.map(captain => [captain]);
    }

    const strongPlayers = remainingPlayers.filter(player => player.rating >= 4);
    const weakPlayers = remainingPlayers.filter(player => player.rating <= 2);
    const neutralPlayers = remainingPlayers.filter(player => player.rating === 3);

    const shuffledStrongPlayers = shuffleArray(strongPlayers);
    const shuffledWeakPlayers = shuffleArray(weakPlayers);
    const shuffledNeutralPlayers = shuffleArray(neutralPlayers);

    const combinedPlayers = [...shuffledStrongPlayers, ...shuffledWeakPlayers, ...shuffledNeutralPlayers];

    const startTeam = Math.floor(Math.random() * numTeams);
    combinedPlayers.forEach((player, index) => {
      const targetTeamIndex = (startTeam + index) % numTeams;
      newTeams[targetTeamIndex].push(player);
    });
  
    setTeams(newTeams);
    setIsTeamsAssigned(true);
    setMessage('ℹ️ Drag & drop players to adjust team lineup or choose a new captain.');
  };
  
  const shuffleArray = (array) => {
    const clonedArray = [...array];
    for (let i = clonedArray.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [clonedArray[i], clonedArray[j]] = [clonedArray[j], clonedArray[i]];
    }
    return clonedArray;
  };

  const resetTeam = () => {
    // Reset all players' ratings to 3
    const resetPlayers = players.map(player => ({
      ...player,
      rating: 3
    }));
    setPlayers(resetPlayers);
    setTeams([]);
    setCurrentVariance(Infinity);
    setMessage('');
    setPreviousTeams([]);
    setIsTeamsAssigned(false);
  };  
  
  const undoShuffle = () => {
    if (previousTeams.length > 0) {
      setTeams(previousTeams);
      setMessage('Reverted to the previous shuffle.');
    } else {
      setMessage('No previous shuffle to undo.');
    }
  };
  
  const handlePlayerChange = (newPlayers) => {
    setPlayers(newPlayers);
    setCanRandomize(true);
  };

  const toggleDescriptionCollapse = () => {
    setIsDescriptionCollapsed(!isDescriptionCollapsed);
  };

  const calculateAverageRating = (team) => {
    if (!team || team.length === 0) return 0;
    const totalRating = team.reduce((acc, player) => {
      const rating = player && player.rating ? parseFloat(player.rating) : 0;
      return acc + (isNaN(rating) ? 0 : rating);
    }, 0);

    const average = totalRating / team.length;
    return isNaN(average) ? 0 : average;
  };

  const copyNamesToClipboard = () => {
    const formattedNames = teams.map((team, index) => {
      const teamHeader = `Team ${index + 1}`;
      const teamNames = team.map((player, playerIndex) => {
        return playerIndex === 0 ? `${player.name} (C)` : player.name;
      }).join('\n');
      
      return `${teamHeader}\n${teamNames}`;
    }).join('\n\n');
  
    navigator.clipboard.writeText(formattedNames)
      .then(() => alert("Team names copied to clipboard!"))
      .catch(err => console.error("Error copying to clipboard:", err));
  };

  return (
    <div className="App container">
      <h1>Serious Futsal Game</h1>

      <p className="info text-primary font-weight-bold">
        ℹ️ Keep all player ratings the same to create random teams balanced only by rating.
      </p>

      <div className="mb-3">
        <button className="btn btn-info" onClick={toggleDescriptionCollapse}>
          {isDescriptionCollapsed ? "Show" : "Hide"} How This App Works
        </button>
        <div className={`collapse ${!isDescriptionCollapsed ? 'show' : ''}`}>
          <div className="mt-3">
            <p>This app helps you balance futsal teams based on player ratings.</p>
            <p>Players are rated on a scale from <strong>1 to 5</strong>:</p>
            <ul>
              <li><strong>1</strong> - Weak</li>
              <li><strong>3</strong> - Neutral (default)</li>
              <li><strong>5</strong> - Strong</li>
            </ul>
            <p>To maintain fair teams, strong and weak players are evenly distributed across teams.</p>
            <p>Players with a neutral rating are randomly assigned to teams. You can adjust player ratings and recalculate teams for better balance.</p>
          </div>
        </div>
      </div>

      <div className="mb-3">
          <label htmlFor="namesText">Paste list from Telegram:</label>
          <textarea
            id="namesText"
            value={namesText}
            onChange={handleNamesTextChange}
            placeholder={`7 Nov, THURSDAY, SAFRA TAMPINES, 9PM - 11PM. Pitch 4\n1. Name\n2. Name...`}
            className="form-control"
            rows="4"
            style={{ width: '100%' }}
          />
        <button className="btn btn-primary mt-2" onClick={populateTableFromNames}>
          Go
        </button>
      </div>

      <div className="mb-3">
        <button className="btn btn-secondary" onClick={toggleTableCollapse}>
          {isTableCollapsed ? "Show Player Input Table" : "Hide Player Input Table"}
        </button>
      </div>

      {!isTableCollapsed && (
        <PlayerTable players={players} setPlayers={handlePlayerChange} showDetails={true} />
      )}

      {isTablePopulated && (
        <div className="mb-1">
          <label>Number of Players:</label>
          <div className="input-group mb-1">
            <button className="btn btn-outline-secondary" onClick={decrementPlayers}>-</button>
            <input
              type="number"
              value={numPlayers}
              readOnly
              className="form-control"
            />
            <button className="btn btn-outline-secondary" onClick={incrementPlayers}>+</button>
          </div>
        </div>
      )}

      <div className="mb-1">
        <label>Number of Teams:</label>
        <div className="input-group mb-1">
          <button className="btn btn-outline-secondary" onClick={decrementTeams}>-</button>
          <input
            type="number"
            id="numTeams"
            value={numTeams}
            onChange={(e) => setNumTeams(parseInt(e.target.value, 10))}
            min="2"
            className="form-control"
          />
          <button className="btn btn-outline-secondary" onClick={incrementTeams}>+</button>
        </div>
      </div>

      <div className="form-check mt-3">
        <input
          className="form-check-input"
          type="checkbox"
          checked={showDetails}
          onChange={() => setShowDetails(!showDetails)}
        />
        <label className="form-check-label">
          Show Rating in Team Columns
        </label>
      </div>

      <div className="form-check mt-1">
        <input
          className="form-check-input"
          type="checkbox"
          checked={lockCaptains}
          onChange={() => setLockCaptains(!lockCaptains)}
        />
        <label className="form-check-label">
          Lock Captains (Keep Captains in Same Teams)
        </label>
      </div>

      <div className="d-flex flex-wrap mt-3">
      {!isTeamsAssigned && (
          <button className="btn btn-primary mr-2" onClick={calculateTeams}>
            Assign Teams
          </button>
        )}
        <button className="btn btn-secondary mr-2" onClick={resetTeam}>
          Reset Teams
        </button>
        <button className="btn btn-danger" onClick={undoShuffle}>
          Undo Shuffle
        </button>
      </div>

      {message && (
        <p className="text-danger font-weight-bold mt-3">
          {message}
        </p>
      )}

        {teams.length > 0 && (
          <>
            <div className="d-flex flex-wrap mt-1">
              <button className="btn btn-info" onClick={copyNamesToClipboard}>
                Copy Names
              </button>
              <button className="btn btn-primary ml-2" onClick={calculateTeams}>
                Reshuffle Teams
              </button>
            </div>
            <TeamDisplay teams={teams} showDetails={showDetails} calculateAverageRating={calculateAverageRating} setTeams={setTeams} lockCaptains={lockCaptains} setPreviousTeams={setPreviousTeams} />
          </>
        )}
      <Footer />
    </div>
  );
}

export default App;
