import React from 'react';
import { useDrag, useDrop, DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import './TeamDisplay.css';

const PlayerRow = ({ player, index, movePlayer, showDetails, isCaptain }) => {
  const [, ref] = useDrag({
    type: 'player',
    item: { index }
  });

  const [, drop] = useDrop({
    accept: 'player',
    hover: (item) => {
      if (item.index !== index) {
        movePlayer(item.index, index);
        item.index = index;
      }
    }
  });

  return (
    <tr ref={(node) => ref(drop(node))}>
      <td>
        {isCaptain ? <strong>{player.name} (C)</strong> : player.name}
        <span className="drag-handle" aria-label="Drag Handle"> ⋮⋮</span>
      </td>
      {showDetails && <td>{player ? player.rating : 'N/A'}</td>}
    </tr>
  );
};

const TeamDisplay = ({ teams, showDetails, calculateAverageRating, setTeams, lockCaptains, setPreviousTeams }) => {
  const allPlayersRatedThree = (team) => {
    return team.every(player => player && parseFloat(player.rating) === 3);
  };

  const movePlayer = (teamIndex, fromIndex, toIndex) => {
    setTeams(prevTeams => {
      const newTeams = [...prevTeams];
      const team = [...newTeams[teamIndex]];
      
      // Remove the moved player and insert at the target position
      const [movedPlayer] = team.splice(fromIndex, 1);
      team.splice(toIndex, 0, movedPlayer);
  
      // Update captain if lockCaptains is true and toIndex is 0
      if (lockCaptains && toIndex === 0) {
        // Update previousTeams to reflect the new captain
        setPreviousTeams(prevTeams => {
          const updatedPreviousTeams = [...prevTeams];
          updatedPreviousTeams[teamIndex] = [...team];
          return updatedPreviousTeams;
        });
      }
  
      newTeams[teamIndex] = team;
      return newTeams;
    });
  };  

  return (
    <DndProvider backend={HTML5Backend}>
      <div className="teams-container d-flex justify-content-around flex-wrap">
        {teams.map((team, teamIndex) => {
          const validPlayers = team.filter(player => player.name.trim() !== '');
          
          return (
            <div key={teamIndex} className="team-card">
              <h4>
                Team {teamIndex + 1}: {validPlayers.length} players
              </h4>

              {!allPlayersRatedThree(team) && (
                <h4>
                  Average Rating: {calculateAverageRating(team).toFixed(2)}
                </h4>
              )}

              <table className="team-display-table">
                <thead>
                  <tr>
                    {showDetails && (
                      <>
                        <th>Name</th>
                        <th>Rating</th>
                      </>
                    )}
                  </tr>
                </thead>
                <tbody>
                  {validPlayers.map((player, playerIndex) => (
                    <PlayerRow
                      key={player.id}
                      player={player}
                      index={playerIndex}
                      movePlayer={(fromIndex, toIndex) => movePlayer(teamIndex, fromIndex, toIndex)}
                      showDetails={showDetails}
                      isCaptain={playerIndex === 0} // Mark the first player as the captain
                    />
                  ))}
                </tbody>
              </table>
            </div>
          );
        })}
      </div>
    </DndProvider>
  );
};

export default TeamDisplay;
