import React from 'react';
import { useTable } from 'react-table';

const PlayerTable = ({ players, setPlayers, showDetails }) => {

  const handleInputChange = (rowId, columnId, value) => {
    setPlayers((prevPlayers) =>
      prevPlayers.map((player) =>
        player.id === rowId ? { ...player, [columnId]: value } : player
      )
    );
  };

  // Different cell rendering logic for the "Rating" column
  const renderEditable = (cell) => {
    if (cell.column.id === 'rating') {
      return (
        <select
          value={cell.value || 3}
          onChange={(e) => handleInputChange(cell.row.original.id, cell.column.id, e.target.value)}
          style={{ width: '120px' }}
        >
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3 (Neutral)</option>
          <option value="4">4</option>
          <option value="5">5</option>
        </select>
      );
    }
    // Default for other columns (e.g., "Name")
    return (
      <input
        value={cell.value || ''}
        onChange={(e) => handleInputChange(cell.row.original.id, cell.column.id, e.target.value)}
        style={{ width: '120px' }}
      />
    );
  };

  const columns = React.useMemo(() => {
    const baseColumns = [
      {
        Header: 'Name',
        accessor: 'name',
        Cell: renderEditable,
      },
      {
        Header: 'Rating',
        accessor: 'rating',
        Cell: renderEditable,
      }
    ];

    return baseColumns;
  }, [showDetails]);

  const data = React.useMemo(() => players, [players]);

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({
    columns,
    data,
  });

  return (
    <table {...getTableProps()} className="player-table table-striped table-bordered">
      <thead>
        {headerGroups.map(headerGroup => (
          <tr {...headerGroup.getHeaderGroupProps()}>
            {headerGroup.headers.map(column => (
              <th {...column.getHeaderProps()}>{column.render('Header')}</th>
            ))}
          </tr>
        ))}
      </thead>
      <tbody {...getTableBodyProps()}>
        {rows.map(row => {
          prepareRow(row);
          return (
            <tr {...row.getRowProps()}>
              {row.cells.map(cell => (
                <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
              ))}
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default PlayerTable;
